// import { EditOutlined, EllipsisOutlined, SettingOutlined } from '@ant-design/icons';
import { Avatar, Card, Skeleton, Typography, Divider, Space } from 'antd'
import React, { useEffect, useState } from 'react'
const { Meta } = Card
export interface ILoadingProps {
  type: string
  location: string
  contactNo: number
  image: string
  price: number
}
export const LoadingCard = (props: ILoadingProps) => {
  const { type, location, contactNo, image, price } = props
  const { Title } = Typography
  const [loading, setLoading] = useState(true)
  useEffect(() => {
    setTimeout(() => {
      setLoading(false)
    }, 2000)
  }, [])

  return (
    <>
      <Card
        style={{ width: 'fit-content', marginTop: 16 }}
        // actions={[
        //   <SettingOutlined key="setting" />,
        //   <EditOutlined key="edit" />,
        //   <EllipsisOutlined key="ellipsis" />,
        // ]}
      >
        <Skeleton loading={loading} avatar active>
          <Meta
            avatar={
              <Avatar
                src='https://joeschmoe.io/api/v1/random'
                style={{ height: 100, width: 100 }}
              />
            }
            title={type}
            description='This is the description'
          />
          <Space style={{ paddingTop: 12 }}>
            <Title level={5}>{type}</Title>
            <Divider
              type='vertical'
              style={{ backgroundColor: 'black', width: 2, top: -3 }}
            ></Divider>
            <Title level={5}>{price} / month</Title>
          </Space>
          <br/>
          <Space direction='vertical'>
              <Title level={5}>{location}</Title>
              <Title style={{color:'blue'}} level={5}>{contactNo}</Title>
          </Space>
        </Skeleton>
      </Card>
    </>
  )
}

export default LoadingCard

LoadingCard.defaultProps = {
  image: 'https://joeschmoe.io/api/v1/random',
  location: 'banglore',
  price: 2500,
  type: '2 BHK',
  contactNo: 964444
}
